.contact__container {
  background-image: url("./../../assets/background.png");
  height: 100%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: #FFFFFFC9;
  background-blend-mode: screen;
}

.contact__container-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px 0;
  background: transparent;

  .form__header {
    font-size: 38px;
    text-align: center;
  }

  .form__img {
    margin: 10% 0;
  }

  .succes__message {
    color: green;
    margin-bottom: 10%;
    line-height: 1.2;
    font-weight: bold;
    width: 35%;
    text-align: center;
  }
}

.form__wrapper {
  display: flex;
  margin-bottom: 8%;
  justify-content: space-between;
  width: 470px;
  background: transparent;

  .form__wrapper-box {
    display: flex;
    flex-direction: column;
    height: 60px;

    .form__wrapper-label {
      font-size: 16px;
    }

    .form__wrapper-input {
      font-size: 18px;
      color: $color-grey;
      border: none;
      border-bottom: 0.75px solid $color-grey;
      background: transparent;
    }

    .form__wrapper-active {
      font-size: 18px;
      color: $color-grey;
      border: none;
      border-bottom: 0.75px solid red;
      background: transparent;
    }
  }
}

.form__wrapper-area {
  display: flex;
  flex-direction: column;

  .area__label {
    font-size: 18px;
  }

  .area__input {
    border: none;
    border-bottom: 0.75px solid $color-grey;
    resize: none;
    color: $color-grey;
    font-family: $font-base;
    background: transparent;
  }

  .area__input-active {
    border: none;
    border-bottom: 0.75px solid red;
    resize: none;
    color: $color-grey;
    font-family: $font-base;
    background: transparent;
  }
}

.form__button-wrapper {
  display: flex;
  justify-content: flex-end;

  .button__submit {
    background-color: transparent;
    font-size: 18px;
    padding: 10px 20px;
    border: 0.75px solid $color-darkgrey;
    font-weight: 300;
    margin-top: 25px;
    height: 50px;
    width: 150px;
    cursor: pointer;
  }
}

.footer {
  margin-top: 2%;
  display: flex;

  .footer__header {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .footer__logos {
    display: flex;
    justify-content: flex-end;

    .footer__img-insta {
      margin-right: 25%;
      margin-left: 2%;
    }
  }
}

.error__message {
  color: red;
  font-size: 12px;
  font-weight: bold;
  height: 13px;
  margin-top: 5px;
}

@media (max-width: 1300px) {
  .contact__content {
    display: flex;
    justify-content: center;

    #contact__image {
      display: none;
    }
  }
  .contact__container-form {
    .form__header {
      font-size: 28px;
    }
  }
}

@media (max-width: 1000px) {
  .contact__container {
    background: transparent;
  }
}

@media (max-width: 600px) {
  .form__wrapper {
    flex-direction: column;
    width: 350px;
  }
  .form__button-wrapper {
    justify-content: center;

    .button__submit {
      width: 100%;
      height: 80px;
    }
  }
}
