.summary {
  font-family: $font-base;
  margin-top: 3%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin-left: 8%;
  position: relative;
}

.summary__container-header {

  .summary__header {
    font-weight: 600;
    color: black;
    font-size: 36px;
    margin-bottom: 5%;
  }

  .summary__counter {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 2%;
  }

  .summary__wrapper {
    display: flex;
    align-items: center;
    margin: 2% 0;

    .summary__img {
      width: 50px;
      height: 50px;
    }

    .summary__description {
      font-size: 24px;
      font-weight: 400;
    }
  }
}

.summary__container-data {
  display: flex;
  width: 100%;
}

.summary__data-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  padding: 7% 0;

  .summary__data-text {
    max-width: 100px;

    &:nth-of-type(1) {
      margin-right: 40px;
    }
  }
}

.summary__data-handler {
  &:nth-of-type(2) {
    margin-left: 20%;
  }
}

.data__handler-header {
  font-size: 24px;
  color: black;
  font-weight: 600;
  margin-bottom: 4%;
}

.textarea__box {
  height: 50px;
  max-width: 300px;
  word-break: break-all;
  white-space: normal;
}

@media (max-width: 800px) {
  #summary {
    height: 1200px;
  }
  .summary__container-header {
    .summary__header {
      font-size: 28px;
    }
  }
  .summary__container-data {
    flex-direction: column;
    margin-top: 20px;
  }
  .summary__data-handler:nth-of-type(2) {
    margin: 0;
  }
}

@media (max-width: 500px) {
  .summary__container-header {
    .summary__header {
      font-size: 18px;
    }
  }

}