.btn__container {
  margin-top: 120px;
  position: absolute;
  top: 65%;
  display: flex;

  .btn {
    font-size: 24px;
    font-weight: 300;
    border: 0.75px solid $color-darkgrey;
    background: transparent;
    font-family: $font-base;
    cursor: pointer;
    height: 60px;
    width: 180px;

    &:nth-of-type(2) {
      margin-left: 12%;
    }
  }
}

@media (max-width: 600px) {
  .btn__container {
    margin-top: 10px;
    flex-direction: column;
    justify-content: center;

    .btn {
      width: 300px;
      height: 50px;
      margin: 10px 0;

      &:nth-of-type(2) {
        margin-left: 0;
      }
    }
  }
}